<template>
    <CollapsePanel class="gap-2" :expand="expand">    
        <div align-left slot="header" class="clearfix">
            <span></span>            
        </div>
        <el-form :model="item" label-width= "180px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-select v-model="item.type" placeholder="">
                            <el-option
                                v-for="item in agentTypes"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">                        
                         <el-date-picker
                            v-model="item.startDate"
                            type="date"
                            placeholder="">
                         </el-date-picker>
                    </el-form-item>                                                         
                </el-col>
                <el-col :span="12">  
                    <el-form-item label="">              
                        <el-select v-model="item.parent" placeholder="">
                            <el-option
                                v-for="item in parentAgents"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>   
                    <el-form-item label="">
                        <el-date-picker
                            v-model="item.endDate"
                            type="date"
                            placeholder="">
                         </el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>                        
        </el-form>
    </CollapsePanel>
</template>
<script>

import CollapsePanel from '@/components/common/CollapsePanel';
/**
 * 
 * TODO:
 *  1. 
 */
export default {
    name: "agentBasicInfo",
    props: ['item', 'mode'],
    components: {CollapsePanel},
    data() {
        return {
            expand: true,
            agentTypes: [{
                label: "",
                value: "0"
            },{
                label: "",
                value: "1"
            },{
                label: "",
                value: "2"
            }],
            parentAgents: [{
                label: "",
                value: "0"
            },{
                label: "",
                value: "1"
            },{
                label: "A",
                value: "2"
            },{
                label: "B",
                value: "3"
            },{
                label: "C",
                value: "4"
            }]
        };
    }
}
</script>
<style scoped>
.el-select, .el-date-editor {
    width: 100%;
}
</style>


