<template>
    <CollapsePanel class="gap-2" :expand="true">
        <div align-left slot="header" class="clearfix">
            <span></span>            
        </div>
        <el-form :model="item" label-width= "180px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="BD">                        
                        <el-select
                            v-model="selectedBD"                            
                            filterable
                            remote                            
                            placeholder=""
                            :remote-method="getBDs"
                            :loading="bdsLoading">
                            <el-option
                                v-for="item in bds"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="selectedCX"                            
                            filterable
                            remote                            
                            placeholder=""
                            :remote-method="getCXs"
                            :loading="cxsLoading">
                            <el-option
                                v-for="item in cxs"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="11">
                        <el-select
                            v-model="selectedDirector"                            
                            filterable
                            remote                            
                            placeholder=""
                            :remote-method="getDirectors"
                            :loading="directorsLoading">
                            <el-option
                                v-for="item in bds"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="xxx xxx">                        
                    </el-form-item>
                    <el-form-item label="xxx xxx">                        
                    </el-form-item>
                    <el-form-item label="xxx xxx">                        
                    </el-form-item>
                </el-col>
            </el-row>            
        </el-form>
    </CollapsePanel>
</template>
<script>
    // TODO: mode
    import CollapsePanel from '@/components/common/CollapsePanel';
    // 
    export default {
        name: "serviceStaffInfo",
        components: {CollapsePanel},
        props: ['item', 'mode'],
        data() {
            return {
                bds: [],
                cxs: [],
                directors: [],
                bdsLoading: false,
                cxsLoading: false,
                directorsLoading: false,
                selectedBD: "",
                selectedCX: "",
                selectedDirector: ""
            };
        },
        methods: {
            getBDs(keyword) {
                // TODO: bd
                //bds = [];
                let tmpArr = [];
                for(let i = 1; i < 10; i++){
                    tmpArr.push({label: keyword + "-" + i, value: i});
                }    
                this.bds = tmpArr;            
            },
            getCXs(keyword) {
                // TODO: 
                let tmpArr = [];
                for(let i = 1; i < 10; i++){
                    tmpArr.push({label: keyword + "-" + i, value: i});
                }    
                this.cxs = tmpArr;

            },
            getDirectors(keyword) {
                // TODO 11
                let tmpArr = [];
                for(let i = 1; i < 10; i++){
                    tmpArr.push({label: keyword + "-" + i, value: i});
                }    
                this.directors = tmpArr;
            }
        },
        watch: {
            selectedBD() {
                // TODO: idbdsbd

            },
            selectedCX() {
                // TODO: idCXs
            },
            selectedDirector() {
                // TODO: iddirectorsdirector
            }
        }

    }
</script>
<style scoped>
.el-select {
    width: 100%;
}
</style>


