<template>
    <CollapsePanel class="gap-2" :expand="expand">    
        <div align-left slot="header" class="clearfix">
            <span></span>            
        </div>
        <el-form :model="item" label-width= "180px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-switch
                            v-model="item.status"
                            active-text=""
                            inactive-text="">
                        </el-switch>
                    </el-form-item>                    
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-select v-model="item.type" placeholder="">
                            <el-option
                                v-for="item in types"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>                    
                </el-col>
            </el-row>                                    
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.actualPayment">
                            <template slot="append"></template>
                        </el-input>
                    </el-form-item>                    
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-checkbox v-model="checked"></el-checkbox>
                        <el-checkbox v-model="checked"></el-checkbox>
                    </el-form-item>                    
                </el-col>
            </el-row> 
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.num">                            
                        </el-input>
                    </el-form-item>                    
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.subbankName">                            
                        </el-input>
                    </el-form-item>                    
                </el-col>
            </el-row> 
            <el-form-item label="">
                <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :before-remove="beforeRemove"
                    multiple
                    :limit="3"
                    :on-exceed="handleExceed"
                    :file-list="fileList">
                    <el-button size="small" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                    </el-upload>
            </el-form-item> 
            <el-form-item label="">
                <el-input v-model="item.remark" type="textarea">                            
                </el-input>
            </el-form-item> 
        </el-form>
    </CollapsePanel>
</template>
<script>
/**
 * 
 * TODO:
 */
    import CollapsePanel from '@/components/common/CollapsePanel';
    // 
    export default {
        name: "corporateInfo",
        components: {CollapsePanel},
        props: ['item', 'mode'],
        data() {
            return {
                expand: true,
                types: [{
                    label: "",
                    value: ""
                },{
                    label: "",
                    value: ""
                },{
                    label: "",
                    value: ""
                },]
            };
        },
        methods: {
            
        },
        watch: {
            
        }
    }
</script>
<style scoped>

</style>


